import React, { useEffect, useState } from "react";
import { Alert, Snackbar } from '@mui/material';
import { useTranslation } from "react-i18next";
import { rtlLanguage } from 'common/utils'

import styles from "./ErrorMessage.module.scss";

export default function ErrorMessage(props) {
  const [open, setOpen] = useState(true);
  const [dir, setDir] = useState('ltr')
  const handleClose = () => {
    setOpen(false);
  };
  const { i18n } = useTranslation('common');

  useEffect(() => {
    if(rtlLanguage.includes(i18n.language)) {
      setDir('rtl')
    }else{
      setDir('ltr')
    }
  },[i18n.language])

  if (props.variant === 'toast') {
    return (
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert variant="standard" severity={props.severity || "error"} onClose={handleClose} dir={dir}>
          {props.error.message}
        </Alert>
      </Snackbar>
    );
  } else {
    return (
      <div className={`error-page ${props.className || ''}`}>
        <div className={styles.errorContent}>
          <Alert severity={props.severity || "error"} variant="standard" dir={dir}>
            {props.error.message}
          </Alert>
        </div>
      </div>
    );
  }



}
