import React, { useRef } from "react";
import FormControlLabel from "@mui/material/FormControlLabel";
import TextField from "@mui/material/TextField";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import { useTheme } from '@mui/material/styles';

import Validation from "components/Validation";
import Radio from "@mui/material/Radio";
import { valueChange } from "surveyEngine/stateMachine";

function ScqChoiceOther(props) {
  const theme = useTheme()
  const nestedTextChild = props.Choice.answers[0]
  const state = useSelector(((state) => {
    let choiceState = state.runState[props.Choice.qualifiedCode]
    let childState = state.runState[nestedTextChild.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isChildDirty = state.template[nestedTextChild.qualifiedCode]
    return {
      showChoice: typeof choiceState?.relevance === "undefined" || choiceState.relevance,
      childInvalid: (show_errors || isChildDirty) && childState?.relevance === true && childState?.validity === false,
      value: childState?.value || ""
    }
  }), shallowEqual);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(valueChange({ componentCode: event.target.name, value: event.target.value }));
  };

  const handleFocus = (event) => {
    dispatch(valueChange({ componentCode: props.parentCode, value: props.Choice.code }));
  };

  const lostFocus = (event) => {
    dispatch({ type: "SET_DIRTY", componentCode: event.target.name });
  };

  const textInput = useRef();
  const onButtonClick = (event) => {
    if (event.target.checked) {
      textInput.current.focus();
    }
  };

  const showChoice = () => {
    return (
      <div className='text-left d-flex'>
        <FormControlLabel
          key={props.Choice.qualifiedCode}
          control={<Radio />}
          label={
            <div className="w-100">
              <TextField
                variant="standard"
                required={state.textChild?.relevance && nestedTextChild.validation?.required}
                inputRef={textInput}
                id={nestedTextChild.qualifiedCode}
                name={nestedTextChild.qualifiedCode}
                label={props.Choice.content.label}
                onChange={handleChange}
                onFocus={handleFocus}
                onBlur={lostFocus}
                value={state.value}
                InputProps={
                  { sx: { fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size } }
                }
                helperText={(state.childInvalid) ? <Validation component={nestedTextChild} limit={1} /> : ""}
              /></div>}
          onChange={onButtonClick}
          value={props.Choice.code} />
        <p />
      </div>
    );
  };

  return (state.showChoice) ? showChoice() : ""

}

export default ScqChoiceOther;
