import React from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import FormControl from "@mui/material/FormControl";
import RadioGroup from "@mui/material/RadioGroup";
import ScqChoice from "components/SCQChoice";
import ScqChoiceOther from "components/ScqChoiceOther";
import Validation from "components/Validation";
import { valueChange } from "surveyEngine/stateMachine";

function SCQ(props) {
  const state = useSelector(((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isDirty = state.template[props.component.qualifiedCode]
    return {
      value: questionState?.value || "",
      showValidation: (show_errors || isDirty) && questionState?.validity === false
    }
  }), shallowEqual);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(valueChange({ componentCode: event.target.name, value: event.target.value }));
  };
  return <FormControl component="fieldset">
    <RadioGroup
      name={props.component.qualifiedCode}
      value={state.value}
      onChange={handleChange}>
      {props.component.answers.map((option) => {
        if (option.type === "other") {
          return <ScqChoiceOther parentCode={props.component.qualifiedCode} key={option.qualifiedCode} Choice={option} />
        } else {
          return <ScqChoice key={option.qualifiedCode} Choice={option} />
        }
      })}
    </RadioGroup>
    {(state.showValidation && props.isSubQuestion) ? <div className="MuiFormHelperText-root" ><Validation component={props.component} limit={1} /> </div> : ""}
  </FormControl>

}

export default SCQ;
