import React from "react";
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import { shallowEqual, useDispatch, useSelector } from "react-redux";

import Content from "components/Content";
import Radio from "@mui/material/Radio";
import Validation from "components/Validation";
import { useTheme } from "@mui/material";
import { valueChange } from "surveyEngine/stateMachine";

function SCQArrayRow(props) {
  const theme = useTheme()
  const state = useSelector(((state) => {
    return {
      value: state.runState[props.answer.qualifiedCode]?.value,
      relevance: state.runState[props.answer.qualifiedCode]?.relevance,
      show_errors: state.runState.Survey.show_errors,
      validity: state.runState[props.answer.qualifiedCode]?.validity,
    }
  }), shallowEqual);
  const dispatch = useDispatch();

  const invalid = (state.show_errors || state.isDirty) && state.validity === false;

  const handleChange = (event) => {
    dispatch(valueChange({ componentCode: event.target.name, value: event.target.value }));
  };


  return typeof state.relevance === 'undefined' || state.relevance ? <React.Fragment>
    <TableRow key={props.answer.code}>
      <TableCell
        sx={{ fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size }}
      >
        {props.answer.content.label}
      </TableCell>
      {props.choices.map((option) => {
        return (
          <TableCell key={option.code} component="th" scope="row" >
            <Radio
              name={props.answer.qualifiedCode}
              onChange={handleChange}
              checked={state.value === option.code}
              value={option.code}
            />
          </TableCell>
        )
      })}
    </TableRow>
    {invalid ? <TableRow>
      <TableCell colSpan={props.choices ? props.choices.length + 1 : 1} ><Validation component={props.answer} /></TableCell >
    </TableRow> : ""}
  </React.Fragment> : ""

}

export default SCQArrayRow;
