import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { shallowEqual, useDispatch } from "react-redux";
import { MenuItem, Select } from "@mui/material";
import { langChange } from "surveyEngine/stateMachine";

function ChangeLang(props) {
  const state = useSelector((state) => {
    return {
      navigate: state.runState.navigate,
      lang: state.runState.lang,
    };
  }, shallowEqual);
  const dispatch = useDispatch();

  useEffect(() => {
    if (typeof state.navigate !== "undefined") {
      props.navigate(state.navigate);
    }
  });

  return (
    <Select
      sx={{ backgroundColor: 'background.paper', color: 'primary.main' }}
      id="ChangeLang"
      value={props.lang.code}
      onChange={(event) => {
        dispatch(langChange({
          lang: event.target.value,
        }));
      }}
    >
      <MenuItem value={props.lang.code}>{props.lang.langName}</MenuItem>
      {props.additionalLang ? props.additionalLang.map((lang, index) => { return <MenuItem key={index} value={lang.code}>{lang.langName}</MenuItem> }) : ""}
    </Select>
  );
}

export default ChangeLang;
