import React from "react";
import FormControlLabel from "@mui/material/FormControlLabel";
import { useDispatch, useSelector, shallowEqual } from "react-redux";

import Checkbox from "@mui/material/Checkbox";
import { useTheme } from "@mui/material";
import { Box } from "@mui/system";
import { valueChange } from "surveyEngine/stateMachine";

function McqAnswer(props) {
  const theme = useTheme()
  const state = useSelector((state) => {
    let answerState = state.runState[props.Answer?.qualifiedCode]
    return {
      showAnswer: typeof answerState?.relevance == "undefined" || answerState.relevance,
      checked: answerState?.value || false
    }
  }, shallowEqual);
  const dispatch = useDispatch();
  const handleChange = (event) => {
    dispatch(valueChange({ componentCode: event.target.name, value: event.target.checked }));
    dispatch({ type: "SET_DIRTY", componentCode: event.target.name });
    dispatch({ type: "SET_DIRTY", componentCode: props.parentCode });
  };

  return (state.showAnswer) ? <FormControlLabel
    control={<Checkbox checked={state.checked} onChange={handleChange} name={props.Answer.qualifiedCode} />}
    label={<Box sx={{ fontFamily: theme.textStyles.text.font, fontSize: theme.textStyles.text.size }}>
      {props.Answer.content.label}
    </Box>}
  /> : ""

}

export default McqAnswer;
