import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { useDispatch, shallowEqual } from "react-redux";
import { useTranslation } from "react-i18next";

import Button from "@mui/material/Button";
import { navigateNext, navigatePrevious } from "surveyEngine/stateMachine";


function Navigation(props) {
  const state = useSelector(((state) => {
    return { navigation: state.runState.navigation,
      has_previous: state.runState.Survey.has_previous,
      has_next: state.runState.Survey.has_next }
  }), shallowEqual);
  const dispatch = useDispatch();
  const { t } = useTranslation('common');

  const previous = () => {
    dispatch(navigatePrevious());
  };
  const next = () => {
    dispatch(navigateNext());
  };

  useEffect(() => {
    if (typeof state.navigation !== "undefined") {
      props.navigate(state.navigation);
    }
  });

  return props.navigationIndex.name == "end" ? "" : (
    <div className="pb-10">
      {state.has_previous ?
        <Button
          variant="contained"
          className="mr-14"
          onClick={() => { previous(); }}
        >
          {t('previous')}
        </Button> : ""
      }
      <Button
        variant="contained"
        onClick={() => { next(); }}
      >
        {state.has_next ? t('next') : t('finish')}
      </Button>
    </div>
  );

}

export default Navigation;
