import React from "react";
import { useSelector } from "react-redux";
import "react-quill/dist/quill.core.css";
import './content.css'
import { rtlLanguage } from "common/utils";

function Content(props) {

  console.log(`rendering Content for: ${props.elementCode}-${props.name}`)
  const isComplex = props.content && props.content.search(/data-instruction/) >= 0
  const state = useSelector(((state) => {
    if (!props.content || !isComplex || !state.runState[props.elementCode] || !props.name || !props.lang) {
      return undefined
    } else {
      return state.runState[props.elementCode][`reference_${props.name}_${props.lang}`]
    }

  }));

  const lang = useSelector(((state) => {
    return state.runState["Survey"].lang
  }));


  const isRtl = rtlLanguage.includes(lang)

  if (!props.content) {
    return ""
  } else if (!isComplex) {
    return <div className={`${isRtl ? "rtl" : "ltr"} ql-editor`} dangerouslySetInnerHTML={{ __html: props.content }} />
  } else {
    return <div className={`${isRtl ? "rtl" : "ltr"} ql-editor`} dangerouslySetInnerHTML={{ __html: replaceMentions(props.content, state) }} />
  }
}

export default Content;

function replaceMentions(html, referenceValue) {
  let doc = document.createElement("div")
  doc.innerHTML = html
  doc.querySelectorAll('span[data-instruction]').forEach(function (el) {
    let attribute = el.getAttribute("data-instruction")
    if (attribute && referenceValue) {
      let attrArray = attribute.split(".")
      if (attrArray && attrArray.length == 2 && referenceValue[attrArray[0]] && referenceValue[attrArray[0]][attrArray[1]]) {
        el.replaceWith(referenceValue[attrArray[0]][attrArray[1]])
      } else {
        el.replaceWith("")
      }
    } else {
      el.replaceWith("")
    }

  });
  return doc.innerHTML
}
