import React from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import TextField from "@mui/material/TextField";

import Validation from "components/Validation";

import styles from "./EmailQuestion.module.scss";
import { useTheme } from '@mui/material/styles';
import { valueChange } from "surveyEngine/stateMachine";

function EmailQuestion(props) {
  const theme = useTheme()
  const state = useSelector((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isDirty = state.template[props.component.qualifiedCode]
    let validity = questionState?.validity
    let invalid = (show_errors || isDirty) && validity === false
    return {
      value: questionState?.value || "",
      invalid: invalid
    };
  }, shallowEqual);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(valueChange({
      componentCode: event.target.name,
      value: event.target.value
    }));
  };

  const lostFocus = (event) => {
    dispatch({ type: "SET_DIRTY", componentCode: event.target.name });
  };

  return (
    <div className={styles.questionItem}>
      <TextField
        variant="standard"
        required={props.component.validation?.validation_required?.isActive ? true : false}
        id={props.component.qualifiedCode}
        name={props.component.qualifiedCode}
        label={props.isSubQuestion ? props.component.content.label : props.component.content.hint || ""}
        onChange={handleChange}
        onBlur={lostFocus}
        inputProps={{ type: "email", maxLength: props.component.maxChars || undefined }}
        value={state.value}
        error={state.invalid}
        InputProps={
          { sx: { fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size } }
        }
        helperText={
          props.isSubQuestion && state.invalid ? <Validation component={props.component} limit={1} /> : ""
        }
      />
    </div>
  );
}

export default EmailQuestion;
