import React from "react";
import { useSelector } from "react-redux";
import ValidationItem from "components/ValidationItem";

function Validation(props) {
  const validation = useSelector((state) => {
    if (!props.component.validation) {
      return {}
    } else {
      var validation = {}
      Object.keys(props.component.validation).forEach(key => {
        var value = state.runState[props.component.qualifiedCode][key]
        if(value){
          validation[key] = value
        }
        
      });
      return validation
    }

  });
  function messages() {
    if (props.component.validation) {
      let array = Object.keys(props.component.validation)
        .filter(key => validation[key])
        let limit = props.limit ? props.limit : array.length
        return array.slice(0,limit).map((key, index) => {
          return <ValidationItem key={index} name={key} validation={props.component.validation[key]}/>
        })
    } else {
      return ""
    }
  }
  return messages()
}

export default Validation;