import React, { useState } from "react";
import Button from "@mui/material/Button";
import Link from "@mui/material/Link";
import { uploadFile, uploadedFileURL } from "surveyEngine/networking";
import { styled, useTheme } from "@mui/material/styles";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import ValidationItem from "components/ValidationItem";
import { valueChange } from "surveyEngine/stateMachine";

const Input = styled('input')({
  display: 'none',
});


function FileUpload(props) {
  const theme = useTheme()

  const state = useSelector(((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    return questionState?.value
  }), shallowEqual);

  const dispatch = useDispatch();

  const [selectedFile, setSelectedFile] = useState();


  const changeHandler = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  const uploadSelectedFile = () => {
    uploadFile(props.component.qualifiedCode, selectedFile).then((response) => {
      resetSelectedFile()
      dispatch(valueChange({ componentCode: props.component.qualifiedCode, value: response.data }));
    }).catch((err) => {
      console.error(err)
    });
  };
  const resetSelectedFile = () => {
    setSelectedFile(undefined);
  };

  let shouldUpload = selectedFile && (!props.component.validation || !props.component.validation.max_file_size
    || props.component.validation.max_file_size.max_size > selectedFile.size / 1000)


  return <div>
    <label htmlFor="contained-button-file">
      <Input id="contained-button-file" type="file" onChange={changeHandler} />
      <Button variant="contained" component="span">
        Choose File
      </Button>
    </label>
    {!selectedFile ? "" : <React.Fragment>
      <span>&nbsp;{selectedFile.name} - {Math.round(selectedFile.size / 1000)}K</span>
      {(shouldUpload) ? <Button variant="text" onClick={uploadSelectedFile}>Upload</Button> : ""}
      <Button
        sx={
          { fontFamily: theme.textStyles.text.font, fontSize: theme.textStyles.text.size }}
        variant="text" onClick={resetSelectedFile}>Cancel</Button>
    </React.Fragment>}
    {!selectedFile || !props.component.validation || !props.component.validation.max_file_size
      || props.component.validation.max_file_size.max_size > (selectedFile.size / 1000) ? "" : <React.Fragment>
      <br />
      <ValidationItem name="max_file_size" validation={props.component.validation.max_file_size} />
    </React.Fragment>}

    {!state || !state.stored_filename ? "" : <React.Fragment>
      <br />
      <Link target="_blank" href={uploadedFileURL(props.component.qualifiedCode, state.filename)}>{state.filename} - {Math.round(state.size / 1000)}K</Link>
    </React.Fragment>}
  </div>
}

export default FileUpload;
