import React from "react";
import { useSelector, shallowEqual } from "react-redux";
import Box from '@mui/material/Box';

import SCQ from "components/Scq";
import TextQuestion from "components/TextQuestion";
import EmailQuestion from "components/EmailQuestion";
import NumberQuestion from "components/NumberQuestion";
import MCQ from "components/Mcq";
import SCQArray from "components/SCQArray";
import FileUpload from "components/FileUpload";
import Validation from "components/Validation";
import ParagraphQuestion from "components/ParagraphQuestion";
import Content from "components/Content";
import DateTimeQuestion from "components/DateTimeQuestion";
import { isRequired } from "common/utils";

import styles from "components/Question/Question.module.scss";
import { useTheme } from "@mui/material";
import ImageScq from "components/ImageScq";
import ImageMcq from "components/ImageMcq";

function Question(props) {
  console.log("rendering: " + props.component.code)
  const theme = useTheme()
  const state = useSelector(((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isDirty = state.template[props.component.qualifiedCode]
    let validity = questionState?.validity
    let invalid = (show_errors || isDirty) && validity === false
    return {
      showQuestion: typeof questionState?.relevance === "undefined" || questionState?.relevance,
      invalid: invalid,
      showValidation: invalid
    }
  }), shallowEqual);

  const showQuestion = () => {
    switch (props.component.type) {
      case "date_time":
        return <DateTimeQuestion key={props.component.qualifiedCode} component={props.component} />;
      case "date":
        return <DateTimeQuestion key={props.component.qualifiedCode} component={props.component} />;
      case "time":
        return <DateTimeQuestion key={props.component.qualifiedCode} component={props.component} />;
      case "scq":
        return <SCQ key={props.component.qualifiedCode} component={props.component} />;
      case "scq_array":
        return <SCQArray key={props.component.qualifiedCode} component={props.component} />;
      case "file_upload":
        return <FileUpload key={props.component.qualifiedCode} component={props.component} />;
      case "mcq":
        return <MCQ key={props.component.qualifiedCode} component={props.component} />;
      case "number":
        return <NumberQuestion key={props.component.qualifiedCode} component={props.component} />;
      case "text":
        return <TextQuestion key={props.component.qualifiedCode} component={props.component} />;
      case "paragraph":
        return <ParagraphQuestion key={props.component.qualifiedCode} component={props.component} />;
      case "email":
        return <EmailQuestion key={props.component.qualifiedCode} component={props.component} />;
      case "image_scq":
        return <ImageScq key={props.component.qualifiedCode} component={props.component} />;
      case "image_mcq":
        return <ImageMcq key={props.component.qualifiedCode} component={props.component} />;
      default:
        return "";
    }
  };

  return (state.showQuestion)
    ? (

      <Box

        sx={{ boxShadow: 2, borderColor: state.invalid ? 'error.main' : 'grey.500', backgroundColor: 'background.paper' }}
        className={`${styles.groupQuestion}`}>

        <Box style={{ fontFamily: theme.textStyles.question.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.question.size }}>
          <Content
            className={`${styles.content} ${styles.question}`}
            name="label" lang={props.lang}
            elementCode={props.component.qualifiedCode}
            content={props.component.content.label}
          />
        </Box>
        {props.component.content.description && props.component.showDescription
          ? <Box className={styles.textDescription} >
            <Content elementCode={props.component.code} name="description" lang={props.lang} content={props.component.content.description} />
          </Box>
          : ""}
        {isRequired(props.component) ?
          <span className={`required ${styles.required}`}>*</span> : null
        }

        {showQuestion()}
        {(!state.showValidation) ? "" :

          <Validation component={props.component} />

        }
      </Box>
    )
    : ""
}

export default Question;
