import React, { useRef } from "react";
import FormControlLabel from "@mui/material/FormControlLabel";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import TextField from "@mui/material/TextField";

import Checkbox from "@mui/material/Checkbox";
import Validation from "components/Validation";
import { useTheme } from '@mui/material/styles';
import { valueChange } from "surveyEngine/stateMachine";

function McqAnswerOther(props) {
  const theme = useTheme()
  const nestedTextChild = props.Answer.answers[0]
  const state = useSelector(((state) => {
    let own = state.runState[props.Answer.qualifiedCode]
    let textChild = state.runState[nestedTextChild.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isChildDirty = state.template[nestedTextChild.qualifiedCode]
    return {
      showAnswer: typeof own?.relevance === "undefined" || own.relevance,
      childInvalid: (show_errors || isChildDirty) && textChild?.relevance === true && textChild?.validity === false,
      checked: own?.value || false,
      textValue: textChild?.value || "",
      textRelevance: state.textChild?.relevance
    }
  }), shallowEqual);

  const dispatch = useDispatch();
  const onButtonClick = (event) => {
    dispatch(valueChange({ componentCode: event.target.name, value: event.target.checked }));
    dispatch({ type: "SET_DIRTY", componentCode: event.target.name });
    dispatch({ type: "SET_DIRTY", componentCode: props.parentCode });
    if (event.target.checked) {
      textInput.current.focus();
    }
  };
  const handleChange = (event) => {
    dispatch(valueChange({ componentCode: event.target.name, value: event.target.value }));
  };

  const textInput = useRef();
  const handleFocus = (event) => {
    dispatch(valueChange({ componentCode: props.Answer.qualifiedCode, value: true }));
  };

  const lostFocus = (event) => {
    dispatch({ type: "SET_DIRTY", componentCode: event.target.name });
  };

  const showAnswer = () => {
    return <FormControlLabel
      control={<Checkbox checked={state.checked}
        onChange={onButtonClick}
        name={props.Answer.qualifiedCode} />}
      label={
        <div className="w-100">
          <TextField
            variant="standard"
            required={state.textRelevance && nestedTextChild.validation?.required}
            inputRef={textInput}
            id={nestedTextChild.qualifiedCode}
            name={nestedTextChild.qualifiedCode}
            label={props.Answer.content.label}
            onChange={handleChange}
            onFocus={handleFocus}
            onBlur={lostFocus}
            value={state.textValue}
            InputProps={
              { sx: { fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size } }
            }
            helperText={(state.childInvalid) ? <Validation component={nestedTextChild} limit={1} /> : ""}
          /></div>}
    />
  }


  return (state.showAnswer) ? showAnswer() : ""
}

export default McqAnswerOther;
