import Api from 'common/api';
import globalObj from 'common/share'

export const navigateFromUserInput = (userInput) => {
    return Api.post(userInput.url, {
        responseId: userInput.responseId,
        navigationMode: userInput.navigationMode,
        lang: userInput.lang,
        navigationDirection: userInput.navigationDirection
    })

}

export const navigateFromPayload = (url, payload, responseId) => {
    console.log(payload)
    return Api.post(url, {
        ...payload,
        responseId: responseId
    })
}

export const uploadFile = (key, selectedFile) => {
    const formData = new FormData();
    formData.append('file', selectedFile);
    return Api.upload(globalObj.userInput.uploadUrl + globalObj.responseId + "/" + key, formData)
}


export const uploadedFileURL = (key, filename) => {
    if (globalObj.userInput) {
        return globalObj.userInput.uploadPreviewUrl + globalObj.responseId + "/" + key + "/" + filename;
    } else {
        return ""
    }

}

export const loadScript = (scriptUrl) => new Promise(resolve => {
  const script = document.createElement("script");
  script.async = false;
  script.src = scriptUrl;
  document.body.appendChild(script);
  script.addEventListener('load', resolve, {
    once: true
  })
})
