import React from "react";
import { Box, CardMedia, useTheme } from "@mui/material";

import ChangeLang from "components/ChangeLang";
import Group from "components/Group";
import Navigation from "components/Navigation";

import styles from "./Survey.module.scss";
import { buildResourceUrl } from "design/GetData";

function Survey(props) {
  const theme = useTheme()
  if (props.navigationIndex.name == "Error") {
    return (
      <Box className={styles.surveyEmpty} >
        Nothing to Show... Check Survey Design
      </Box>
    );
  } else {
    return (
      <form className="survey-page" style={{ fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size }}>
        <ChangeLang lang={props.lang} additionalLang={props.additionalLang} navigate={props.navigate} />
        <div className={styles.surveyContent}>
          <div className={styles.surveyGroups}>
            {props.survey.headerImage ?
              <CardMedia
                className={styles.cardImage}
                component="img"
                image={buildResourceUrl(props.survey.headerImage)}
              /> : null
            }
            {props.survey && props.survey.groups
              ? props.survey.groups.filter((group) => group.inCurrentNavigation).map((group) => (
                <Group key={group.code} group={group} lang={props.lang.code} />
              ))
              : ""}
            <Navigation
              navigationIndex={props.navigationIndex}
              navigate={props.navigate}
            />
          </div>
        </div>
      </form>
    );
  }
}

export default Survey;
