import React from "react";
import FormControl from "@mui/material/FormControl";
import FormGroup from "@mui/material/RadioGroup";
import { useSelector, shallowEqual } from "react-redux";

import McqAnswer from "components/MCQAnswer";
import McqAnswerOther from "components/McqAnswerOther";
import Validation from "components/Validation";

function MCQ(props) {
  const state = useSelector(((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isDirty = state.template[props.component.qualifiedCode]
    let validity = questionState?.validity
    let invalid = (show_errors || isDirty) && validity === false
    return {
      invalid: invalid,
    }
  }), shallowEqual);
  return <FormControl component="fieldset">
    <FormGroup>
      {props.component.answers.map((option) => {
        if (option.type === "other") {
          return <McqAnswerOther key={option.qualifiedCode} Answer={option} parentCode={props.component.qualifiedCode} />
        } else {
          return <McqAnswer key={option.qualifiedCode} Answer={option} parentCode={props.component.qualifiedCode} />
        }
      })}
    </FormGroup>
    {(state.showValidation && props.isSubQuestion) ? <div className="MuiFormHelperText-root" ><Validation component={props.component} limit={1} /> </div> : ""}
  </FormControl>
}

export default MCQ;
