import React from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import styles from './ImageScq.module.scss'
import { valueChange } from "surveyEngine/stateMachine";
import { useTheme } from "@emotion/react";
import { Box, Card, Grid,  Radio } from "@mui/material";
import { buildResourceUrl } from "design/GetData";

function ImageScq(props) {
  const theme = useTheme()
  const state = useSelector(((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isDirty = state.template[props.component.qualifiedCode]
    return {
      value: questionState?.value || "",
      showValidation: (show_errors || isDirty) && questionState?.validity === false
    }
  }), shallowEqual);
  const dispatch = useDispatch();

  const handleChange = (componentCode, value) => {
    dispatch(valueChange({ componentCode, value }));
  };
  return <Grid container className={styles.imageGrid}>
    {props.component.answers.map((option) => {

      return <Grid key={option.code} item xs={12 / props.component.columns}>
        <Card className={styles.imageContainer}
          onClick={() => handleChange(props.component.qualifiedCode, option.code)}
          style={{
            backgroundImage: `url('${buildResourceUrl(option.image)}')`,
            backgroundColor: theme.palette.background.default,
            margin: props.component.spacing + "px",
            height: props.component.imageHeight + "px",
            border: state.value === option.code ? `2px solid ${theme.palette.primary.main}` : "2px solid transparent"
          }}>
          <Radio
            className={styles.selection}
            checked={state.value === option.code}
            onChange={event => handleChange(event.target.name, event.target.value)}
            value={option.code}
            name={props.component.qualifiedCode}
          />
        </Card>
        {<Box sx={{
          fontFamily: theme.textStyles.text.font,
          color: theme.textStyles.text.color,
          fontSize: theme.textStyles.text.size
        }}>
          {option.content.label}
        </Box>}
      </Grid>
    })}
  </Grid>

}

export default ImageScq;
