import React from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import styles from './ImageMcq.module.scss'
import { useTheme } from "@emotion/react";
import { Grid } from "@mui/material";
import ImageMcqItem from "components/ImageMcqItem";

function ImageMcq(props) {
  const theme = useTheme()
  const state = useSelector(((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isDirty = state.template[props.component.qualifiedCode]
    return {
      value: questionState?.value || "",
      showValidation: (show_errors || isDirty) && questionState?.validity === false
    }
  }), shallowEqual);
  const dispatch = useDispatch();

  return <Grid container className={styles.imageGrid}>
    {props.component.answers.map((option) => {
      return <ImageMcqItem 
      option={option} 
      spacing={props.component.spacing}
      columns={props.component.columns}
      parentCode = {props.component.qualifiedCode}
      imageHeight={props.component.imageHeight}
      key={option.qualifiedCode} />
    })}
  </Grid>

}

export default ImageMcq;
