import React, { useEffect } from "react";

import styles from "./SurveyIndex.module.scss";
import { Card } from "@mui/material";
import { Box } from "@mui/system";
import { stripTags } from "design/utils/designUtils";
import { shallowEqual, useSelector } from "react-redux";
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import CircleIcon from '@mui/icons-material/Circle';
import { useTheme } from "@emotion/react";
import { useDispatch } from "react-redux";
import { jump } from "surveyEngine/stateMachine";

function SurveyIndex(props) {
  const theme = useTheme()
  const dispatch = useDispatch()

  const relevance_map = useSelector((state) => {
    return state.runState["Survey"].relevance_map
  }, shallowEqual);

  const navigation = useSelector((state) => {
    return state.runState.navigation
  }, shallowEqual);

  useEffect(() => {
    if (typeof navigation !== "undefined") {
      props.navigate(navigation);
    }
  });

  const validity_map = useSelector((state) => {
    return state.runState["Survey"].validity_map
  }, shallowEqual);

  console.log(props)

  const isCurrentGroup = (groupCode) => {
    return props.navigationIndex.name == "group" && groupCode == props.navigationIndex.groupId
  }

  const isCurrentQuestion = (questionCode) => {
    return props.navigationIndex.name == "question" && questionCode == props.navigationIndex.questionId
  }

  const isGroupClickable = (groupCode) => !isCurrentGroup(groupCode) && props.navigationIndex.name == "group"

  const isQuestionClickable = (questionCode) => !isCurrentQuestion(questionCode) && props.navigationIndex.name == "question"

  const onGroupClicked = (groupCode) => {
    if (isGroupClickable(groupCode)) {
      dispatch(jump({ ...props.navigationIndex, groupId: groupCode }))
    }
  }

  const onQuestionClicked = (questionCode) => {
    if (isQuestionClickable(questionCode)) {
      dispatch(jump({ ...props.navigationIndex, questionId: questionCode }))
    }
  }

  return (
    <div className={styles.surveyContent}>
      {props.survey && props.survey.groups
        ? props.survey.groups.filter(group => relevance_map[group.code] && group.groupType != "END").map((group) => {
          return <Card onClick={() => onGroupClicked(group.code)} key={group.code} className={styles.groupCard} style={{
            backgroundColor: isCurrentGroup(group.code) ? "beige" : theme.palette.background.paper,
            cursor: isGroupClickable(group.code) ? "pointer" : "default"
          }}>
            <Box className={styles.groupTitle}>{stripTags(group.content.label)} </Box>
            {group.questions.filter(question => relevance_map[question.code]).map((question) => {
              return <Box key={question.code} onClick={() => onQuestionClicked(question.code)} className={styles.questionTitle} style={{
                backgroundColor: isCurrentQuestion(question.code) ? "beige" : "inherit",
                cursor: isGroupClickable(group.code) ? "inherit" : isQuestionClickable(group.code) ? "pointer" : "default"
              }}>
                {validity_map[question.code] ? <CheckCircleIcon className={styles.validationIcon} /> : <CircleIcon className={styles.bullet} />}
                {stripTags(question.content.label)}
              </Box>
            })}
          </Card>
        })
        : ""}
    </div>)

}

export default SurveyIndex;
