export const fetchUserInputFromParams = (params) => {
  let lang = params.get('lang');
  let responseId = params.get('response_id');

  let url = "http://" + params.get('url') + "/api/" + params.get('sid') + "/navigate"
  let scriptUrl = "http://" + params.get('url') + "/api/" + params.get('sid') + "/runtime.js"
  let uploadUrl = "http://" + params.get('url') + "/survey/upload/" + params.get('sid') + "/"
  let uploadPreviewUrl = "http://" + params.get('url') + "/survey/" + params.get('sid') + "/upload/"

  let navigationMode = params.get('navigation_mode');
  let navigationDirection = JSON.parse(params.get('navigation_direction'));
  if (!navigationDirection && !responseId) {
    navigationDirection = { "name": "START" }
  } else if (!navigationDirection) {
    navigationDirection = { "name": "RESUME" }
  }
  return {
    url: url, scriptUrl: scriptUrl, uploadUrl: uploadUrl, uploadPreviewUrl: uploadPreviewUrl,
    lang: lang, navigationMode: navigationMode, responseId: responseId,
    navigationDirection: navigationDirection
  }


}