import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import styles from "./RunSurvey.module.scss";
import { useTranslation } from "react-i18next";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import rtlPlugin from "stylis-plugin-rtl";
import { prefixer } from "stylis";
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";

import Layout from "components/Layout";
import ErrorMessage from "components/ErrorMessage";
import globalObj from "common/share";
import Survey from "components/Survey";
import { fetchUserInputFromParams } from "surveyEngine/userInputUtils";
import {
    navigateFromUserInput,
    navigateFromPayload,
    loadScript,
} from "surveyEngine/networking";
import { rtlLanguage } from "common/utils";
import { defualtTheme } from "constants";
import { stateReceived } from "surveyEngine/stateMachine";
import SurveyIndex from "components/SurveyIndex";

function RunSurvey() {

    console.log("RunSurvey")
    const [data, setData] = useState({});
    const [error, setError] = useState(null);
    const { i18n } = useTranslation("common");
    const dispatch = useDispatch();
    let search = window.location.search;
    let params = new URLSearchParams(search);
    let userInput = fetchUserInputFromParams(params);

    const navigate = (payload) => {
        setError(null);
        navigateFromPayload(userInput.url, payload, data.responseId)
            .then((response) => {
                setData({});
                dispatch(stateReceived(response.data));
                i18n.changeLanguage(response.data.lang.code);
                setData(response.data);
            })
            .catch((err) => {
                setError({
                    errorType: "click",
                    message: err.message,
                });
            });
    };

    useEffect(() => {
        if (rtlLanguage.includes(i18n.language)) {
            document.dir = "rtl";
        } else {
            document.dir = "ltr";
        }
    }, [i18n.language]);

    useEffect(() => {
        if (isPropsEmpty()) {
            setError(null);
            loadScript(userInput.scriptUrl).then(() => {
                navigateFromUserInput(userInput)
                    .then((response) => {
                        setData(null);
                        const navigationIndex = response.data.navigationIndex
                        if (navigationIndex.name != "Error") {
                            dispatch(stateReceived(response.data));
                            if (!Object.isFrozen(globalObj)) {
                                globalObj.userInput = userInput;
                                globalObj.responseId = response.data.responseId;
                                Object.freeze(globalObj);
                            }
                        }
                        i18n.changeLanguage(response.data.lang.code);
                        setData(response.data);
                    })
                    .catch((err) => {
                        console.error(err)
                        setError({
                            errorType: "startup",
                            message: err.message,
                        });
                    });
            });
        }
    });

    const theme = createTheme({
        ...defualtTheme(data?.survey?.theme),
        direction: rtlLanguage.includes(i18n.language) ? "rtl" : "ltr",
    });

    const cacheRtl = createCache({
        key: rtlLanguage.includes(i18n.language) ? "muirtl" : "muiltr",
        stylisPlugins: rtlLanguage.includes(i18n.language)
            ? [prefixer, rtlPlugin]
            : null,
    });

    function isPropsEmpty() {
        return !data || (Object.keys(data).length === 0 && data.constructor === Object);
    }


    return <CacheProvider value={cacheRtl}>
        <ThemeProvider theme={theme}>
            <Layout>
                {!isPropsEmpty() && <div className={styles.mainContainer}>
                    <Survey
                        navigate={navigate}
                        key="Survey"
                        survey={data?.survey}
                        navigationIndex={data?.navigationIndex}
                        lang={data?.lang}
                        additionalLang={data?.additionalLang}
                        error={error} />
                    <SurveyIndex
                        navigate={navigate}
                        navigationIndex={data?.navigationIndex}
                        survey={data?.survey} />

                </div>}
                {error ? <ErrorMessage error={error} variant="toast" /> : null}
            </Layout>
        </ThemeProvider>
    </CacheProvider>

}

export default RunSurvey;
