import React from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import styles from './ImageMcqItem.module.scss'
import { valueChange } from "surveyEngine/stateMachine";
import { useTheme } from "@emotion/react";
import { Box, Card, Checkbox, Grid } from "@mui/material";
import { buildResourceUrl } from "design/GetData";

function ImageMcqItem(props) {
  const theme = useTheme()
  const state = useSelector((state) => {
    let answerState = state.runState[props.option.qualifiedCode]
    return {
      showAnswer: typeof answerState?.relevance == "undefined" || answerState.relevance,
      checked: answerState?.value || false
    }
  }, shallowEqual);

  const dispatch = useDispatch();
  const handleChange = (componentCode, value) => {
    dispatch(valueChange({ componentCode, value }));
    dispatch({ type: "SET_DIRTY", componentCode });
    dispatch({ type: "SET_DIRTY", componentCode: props.parentCode });
  };

  return <Grid key={props.option.code} item xs={12 / props.columns}>
    <Card className={styles.imageContainer}
      onClick={() => handleChange(props.option.qualifiedCode, !state.checked)}
      style={{
        backgroundImage: `url('${buildResourceUrl(props.option.image)}')`,
        backgroundColor: theme.palette.background.default,
        margin: props.spacing + "px",
        height: props.imageHeight + "px",
        border: state.checked ? `2px solid ${theme.palette.primary.main}` : "2px solid transparent"
      }}>
      <Checkbox
        className={styles.selection}
        onChange={event => handleChange(props.option.qualifiedCode, !state.checked)}
        checked={state.checked}
      />
    </Card>
    {<Box sx={{
      fontFamily: theme.textStyles.text.font,
      color: theme.textStyles.text.color,
      fontSize: theme.textStyles.text.size
    }}>
      {props.option.content.label}
    </Box>}
  </Grid>


}

export default ImageMcqItem;
