import React from "react";
import { useSelector, shallowEqual } from "react-redux";

import Question from "components/Question";
import Content from "components/Content";

import styles from "./Group.module.scss";
import { Box, useTheme } from "@mui/material";

function Group(props) {
  console.log("rendering: " + props.group.code)
  const theme = useTheme()

  const state = useSelector((state) => {
    let groupState = state.runState[props.group.code]
    return { showGroup: typeof groupState?.relevance === "undefined" || groupState.relevance }
  }, shallowEqual);
  const showGroup = () => {
    return (
      <React.Fragment>
        <Box sx={{borderColor : 'grey.500', boxShadow:1, backgroundColor:'background.paper'}} className="card-custom">
        <Box sx={{backgroundColor:'primary.main', height:'8px'}} className={styles.groupHeader}></Box>
          <Box sx={{backgroundColor:'background.paper'}} className={styles.titleRow}>
            <div className={styles.textHeader} style={{ fontFamily: theme.textStyles.group.font, color: theme.textStyles.group.color, fontSize: theme.textStyles.group.size }} >
              <Content 
               elementCode={props.group.code} name="label" lang={props.lang} content={props.group.content?.label}  />
            </div>
            <Box className={styles.textDescription}>
              <Content elementCode={props.group.code} name="description" lang={props.lang}  content={props.group.content?.description}  />
            </Box>
          </Box>
        </Box>
        {props.group && props.group.questions
          ? props.group.questions.filter((quest) => quest.inCurrentNavigation).map((quest) => (
            <Question key={quest.code} component={quest} lang={props.lang} />
          ))
          : ""}
      </React.Fragment>
    );
  };
  return (state.showGroup) && (props.group ? showGroup() : "");
}

export default Group;
