import React from "react";
import FormControlLabel from "@mui/material/FormControlLabel";
import { useSelector, shallowEqual } from "react-redux";

import Radio from "@mui/material/Radio";
import { Box, useTheme } from "@mui/material";

function ScqChoice(props) {
  const theme = useTheme()
  const state = useSelector(((state) => {
    let choiceState = state.runState[props.Choice?.code]
    return {
      showChoice: typeof choiceState?.relevance === "undefined" || choiceState?.relevance
    }
  }), shallowEqual);

  const showChoice = () => {
    return (
      <FormControlLabel
        key={props.Choice.qualifiedCode}
        control={<Radio />}
        label={<Box sx={{ fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size }}>
          {props.Choice.content.label}
        </Box>}
        value={props.Choice.code}
      />
    );
  };

  return (showChoice) ? showChoice() : ""

}

export default ScqChoice;
