import React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";

import Content from "components/Content";
import SCQArrayRow from "components/SCQArrayRow";
import { useTheme } from "@emotion/react";


function SCQArray(props) {
  const theme = useTheme()
  let columns = props.component.answers.filter(answer => answer.type == "column")
  let rows = props.component.answers.filter(answer => answer.type == "row")

  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell key="content"></TableCell>
            {columns.map((option) => {
              return <TableCell
                sx={{ fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size }}
                key={option.qualifiedCode} >{option.content.label}</TableCell>
            })}
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((answer) => {
            return (
              <React.Fragment key={answer.qualifiedCode}>
                <SCQArrayRow key={answer.qualifiedCode} answer={answer} choices={columns} />
              </React.Fragment>
            )
          })}

        </TableBody>
      </Table>
    </TableContainer>
  );

}

export default SCQArray;
