import React, { useState } from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import { useTranslation } from "react-i18next";
import TextareaAutosize from '@mui/material/TextareaAutosize';

import { MapContainer, TileLayer, Popup, Marker, useMapEvents } from 'react-leaflet'

import styles from "./ParagraphQuestion.module.scss";
import { valueChange } from "surveyEngine/stateMachine";

function ParagraphQuestion(props) {
  const state = useSelector((state) => {
    let questionState = state.runState[props.component.qualifiedCode]
    let show_errors = state.runState.Survey.show_errors
    let isDirty = state.template[props.component.qualifiedCode]
    let validity = questionState?.validity
    let invalid = (show_errors || isDirty) && validity === false
    let value = questionState?.value || ""
    return {
      value: value,
      wordCount: window.FrankieScripts ? window.FrankieScripts.wordCount(value) : 0,
      invalid: invalid
    };
  }, shallowEqual);
  const dispatch = useDispatch();

  const { t } = useTranslation('common');

  const handleChange = (event) => {
    dispatch(valueChange({
      componentCode: event.target.name,
      value: event.target.value
    }));
  };

  const lostFocus = (event) => {
    dispatch({ type: "SET_DIRTY", componentCode: event.target.name });
  };

  function LocationMarker() {
    const [position, setPosition] = useState(null)
    const map = useMapEvents({
      click() {
        map.locate()
      },
      locationfound(e) {
        setPosition(e.latlng)
        map.flyTo(e.latlng, map.getZoom())
      },
      error(e){
        console.log(e)
      }
    })

    return position === null ? null : (
      <Marker position={position}>
        <Popup>You are here</Popup>
      </Marker>
    )
  }


  return (
    <div className={styles.questionItem}>

      <MapContainer style={{
        height: "300px",
        width: "100%"
      }}
        center={{ lat: 51.505, lng: -0.09 }}
        zoom={10}
        scrollWheelZoom={false}>
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          // url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          url="./4uMaps/{z}/{x}/{y}.png"
        />
        <LocationMarker />
      </MapContainer>
      <TextareaAutosize
        className={styles.paragraph}
        required={props.component.validation?.validation_required?.isActive ? true : false}
        id={props.component.qualifiedCode}
        name={props.component.qualifiedCode}
        minRows={props.component.minRows || 2}
        label={props.component.content.hint || ""}
        onChange={handleChange}
        onBlur={lostFocus}
        value={state.value}
      />
      {props.component.showWordCount ? <div className={styles.wordCount}>
        <span>{t("word_count", { "count": state.wordCount })}</span>
      </div>
        : ""}
    </div>

  );
}

export default ParagraphQuestion;
